import { getInfo, setInfo } from '@/utils/storage'

export default {
  namespaced: true,
  state () {
    return {
      userInfo: getInfo()
    }
  },
  mutations: {
    // 所有mutations的第一个参数，都是state
    setUserInfo (state, obj) {
      state.userInfo = obj
      setInfo(obj)
    }
  },
  actions: {
    logout (context) {
      // 重置userInfo
      context.commit('setUserInfo', {})

      // 跨仓库重置cart
      context.commit('cart/setCartList', [], { root: true })
    }
  },
  getters: {}
}
